  font-family: 'Roboto';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
}

:root {
  --lightGrey: #b1b1b1;

  background-color: var(--lightGrey);
}

body {
  font-family: Roboto;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
}

.card {
  position:relative; /*Needed for absolute positioning of line */
  width: 100px;
  height: 100px;
  border: 1px solid #ccc;
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card i {
  font-size: 24px; /* Adjust icon size as needed */
}

.card.horizontal-line::after,
.card.vertical-line::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid transparent;
}

.card.horizontal-line::after {
  /* border- ; */
    border-top-color: rgb(216, 31, 31);
}

.card.vertical-line::after {
    border-left-color: black;
}

.card.diagonal-line::after {
    border-top-color: black;
    border-left-color: black;
    transform: rotate(45deg);
}


/* Hortizontal colours */
.card.horizontal-line:nth-child(1)::after {
  border-top-color: red; /* Change the color for the first card */
}

.card.horizontal-line:nth-child(2)::after {
  border-top-color: blue; /* Change the color for the second card */
}

.card.horizontal-line:nth-child(3)::after {
  border-top-color: green; /* Change the color for the second card */
}

.card.horizontal-line:nth-child(4)::after {
  border-top-color: yellow; /* Change the color for the second card */
}

.card.horizontal-line:nth-child(5)::after {
  border-top-color: purple; /* Change the color for the second card */
}

.card.horizontal-line:nth-child(6)::after {
  border-top-color: cyan; /* Change the color for the second card */
}

.card.horizontal-line:nth-child(7)::after {
  border-top-color: magenta; /* Change the color for the second card */
}

.card.horizontal-line:nth-child(8)::after {
  border-top-color: orange; /* Change the color for the second card */
}


/* Vertical colours. */
.card.horizontal-line-0::after {
  border-left-color: brown; /* Change the color for the first card */
}

.card.horizontal-line-1::after {
  border-left-color: black; /* Change the color for the second card */
}

.card.horizontal-line-2::after {
  border-left-color: indigo; /* Change the color for the second card */
}

.card.horizontal-line-3::after {
  border-left-color: maroon; /* Change the color for the second card */
}

.card.horizontal-line-4::after {
  border-left-color: salmon; /* Change the color for the second card */
}

.card.horizontal-line-5::after {
  border-left-color: cornflowerblue; /* Change the color for the second card */
}

.card.horizontal-line-6::after {
  border-left-color: indianred; /* Change the color for the second card */
}

.card.horizontal-line-7::after {
  border-left-color: royalblue; /* Change the color for the second card */
}
